import http from './axios'
import { api, get, post } from './api'

const config = require('../../public/config/config.js')
const Get = get;
const Post = post;
const request = {}

Get.map(item => {
	request[item] = (params, fn) => {
		http.get(
			config.default.path + api[item], params
		).then(response => {
			fn && fn(response);
		})
	}
})

Post.map(item => {
	request[item] = (params, fn) => {
		http.post(
			config.default.path + api[item], params
		).then(response => {
			fn && fn(response);
		})
	}
})

export default request