<template>
	<div class="set-meal" @click="changeItem(setmeal)">
		<div>
			<h3>{{setmeal.title}}</h3>
			<div class="tags">
				<span class="btn-tag" :key="index" v-for="(tag, index) in setmeal.tag">{{tag}}</span>
			</div>
		</div>
		<div>
			<p :key="index" v-for="(detail, index) in setmeal.details">{{detail}}</p>
		</div>
		<div>
			<div class="m" v-if="setmeal.priceMan"><div class="price">{{setmeal.priceMan | thousandth}}</div><div class="unit">JPY/</div></div>
			<div class="w" v-if="setmeal.priceWoman"><div class="price">{{setmeal.priceWoman | thousandth}}</div><div class="unit">JPY/</div></div>
			<div class="p" v-if="setmeal.price"><div class="price">{{setmeal.price | thousandth}}</div><div class="unit">JPY/</div></div>
			<div class="p" v-if="!setmeal.price && !setmeal.priceMan && !setmeal.priceWoman">+</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				setmeal: {}
			}
		},
		props: ['data'],
		created() {
			if (this.data) this.setmeal = this.data;
		},
		methods: {
			changeItem(item) {
				this.$emit('changeMeal', item);
			}
		},
		watch: {
			data(newValue) {
				this.setmeal = newValue;
			}
		}
	}
</script>

<style lang="scss">
	@import '@/styles/components/setmeal.scss';
</style>